import { redirect } from "next/navigation";
import api from "@/api";

// Aquí eliminamos la opción de `force-static` para hacer la página dinámica
// (sin utilizar force-static)
export const dynamic = true;

export default async function HomePage() {
  // Traemos los mensajes de la API
  const messages = await api.message.list();

  // Función para agregar un nuevo mensaje
  async function add(formData) {
    "use server";

    // Obtenemos el texto del formulario
    const message = formData.get("text");
    // Creamos la preferencia de pago en la API
    const url = await api.message.submit(message);

    // Redirigimos al usuario a la URL proporcionada por la API
    redirect(url);
  }

  return (
    <section className="grid gap-8">
      <form action={add} className="grid gap-2">
        <textarea
          className="border-2 border-blue-400 p-2"
          name="text"
          placeholder="Hola perro"
          rows={3}
        />
        <button className="rounded bg-blue-400 p-2" type="submit">
          Enviar
        </button>
      </form>
      <ul className="grid gap-2">
        {messages.map((message) => (
          <li key={message.id} className="rounded bg-blue-400/10 p-4">
            {message.text}
          </li>
        ))}
      </ul>
    </section>
  );
}
